.item-gris{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
    box-sizing: border-box;
    background-color: #7BDFF2;
    margin: 10px 0;
    border-radius: 20px;
    border: 3px solid #001242;
    height: 50px;
    width: 100%;
}

.circulo-gris{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    background-color: #EFF7F6;
}

.carga{
    /* background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200,199,199)); */
    background-size: 400% 400%;
    animation: cargaLoading 1s infinite;
}

@keyframes cargaLoading {
    0%{
        opacity: 0.9;
        
    }
    50%{
        opacity: 0.6;

    }
    100%{
        opacity: 0.8;

    }
}









.contenedor-cargando{
    display: flex;
    font-size: 2rem;
    font-weight: bold;
}

