@keyframes carga {
    from{
        transform: scale(1.3);
        transform: translate(0px , -4px);
        /* transform: rotateY(2); */
    }
    to{
        transform: scale(1);
        transform: translate(0px , 0px);
        /* transform: rotateY(0); */
    }
}