.agregar{
    font-family: 'Roboto Slab', serif;
    width: 130px;
    height: 40px;
    color: white;
    background-color: #001242;
    border-radius: 20px;
    border: 1px solid transparent;
    font-size: 0.9rem;
    margin: 20px 0;
    cursor: pointer;
}

.agregar-checked:hover{
    color: #001242;
    background-color: white;
    border: 4px solid #001242;
    font-weight: bold;
}