.agre-quit{
    width: 2.2rem;
    height: 2.2rem;
    margin: 0;
    padding: 0;
    background-color: transparent;
    border-color: transparent;
    transform: scale(1);
    cursor: pointer;
}

.agre-quit:hover{
    transform: scale(1.3);
}

