form{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 70%;
    height: 50%;
    padding: 20px;
    border-radius: 16px;
    background: linear-gradient(18deg, rgba(163,193,255,1) 3%, rgba(9,212,255,1) 53%, rgba(14,2,255,1) 100%);
}

.content-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 90%;
    height: 90%;
    background-color: white;
    border-radius: 16px;
}

.text-form{
    font-size: 1.2rem;
    font-family: 'Roboto Slab', serif;
    font-weight: bold;
    color: #000022;
}

textarea{
    border: 2px solid #000022;
    width: 80%;
    height: auto;
    border-radius: 8px;
    padding-left: 10px;
    padding-top: 5px;
}

textarea::placeholder{
    color: #000022;
}

textarea:focus{
    border-color: transparent;
    outline-color: #09d4ff;
}

.buttons{
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    width: 70%;
}

.button-remove, .button-add{
    font-family: 'Roboto Slab', serif;
    font-size: 0.9rem;
    font-weight: bold;
    width: 130px;
    height: 40px;
    border-radius: 20px;
    color: #000022;
    cursor: pointer;
    border: 1px solid transparent;
}

.button-add{
    background: #00e60f;
}

.button-remove{
    background: #dc2626;
}

.button-remove:hover, .button-add:hover{
    opacity: 0.8;
}

.button-remove:checked, .button-add:checked{
    opacity: 0.8;
}