.infor{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
    width: 100%;
}

.titulo-pie{
    font-size: 2rem;
    color: #FDFCDC;
    text-shadow: 0px 1px 8px #FEFEFF;
    font-weight: bold;
    font-style: italic;
    margin: 5px 0;
    padding: 0;
    box-sizing: border-box;
}

.text-pie{
    color: #FEFEFF;
    font-size: 1.1rem;
    text-align: center;
    margin-bottom: 3px;
}