.content-error{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
    box-sizing: border-box;
    background-color: #7BDFF2;
    margin: 10px 0;
    border-radius: 20px;
    border: 3px solid #001242;
    height: 50px;
    width: 100%;
    justify-content: center;
    opacity: .8;
}

.text-error{
    color: #001242;
    font-weight: bold;
}