.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #000022;
    border-radius: 14px 14px 0 0;
    width: 100%;
    margin: 0;
    opacity: 0.9;
}

h1{
    font-size: 2rem;
    color: #FDFCDC;
    text-shadow: 0px 1px 8px #FEFEFF;
    font-weight: bold;
    font-style: italic;
    margin: 0px;
    padding: 20px;
    text-align: center;
}

.nuevas-tareas{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.text-crear {
    color: #000022;
    font-size: 1.1rem;
    text-align: center;
    padding: 10px;
    font-weight: bold;
    margin: 10px 0;
}

.input-crear{
    align-items: center;
    height: 26px;
    width: 85%;
    margin: 0 0 0 20px;
    border: 3px solid #001242;
    background-color: #cedaf3;
    border-radius: 8px;
    margin: 10px 0;
    padding-left: 10px;
}

.input-crear::placeholder{
    font-family: 'Roboto Slab', serif;
    color: #001242;
}

.input-crear:focus{
    border-color: transparent;
    outline-color: #09d4ff;
    background-color: white;
}