.buscar-tareas{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: #001242;
    border-radius: 0 0 15px 15px;
}
.text-buscar {
    /* color: #000022; */
    color: #FDFCDC;
    font-size: 1.1rem;
    text-align: center;
    padding: 10px;
    font-weight: bold;
    margin: 20px 0;
}

.input-buscar{
    align-items: center;
    height: 26px;
    width: 85%;
    margin: 0 0 0 20px;
    border: 4px solid #FDFCDC;
    background-color: #cedaf3;
    border-radius: 8px;
    margin: 10px 0 30px 0;
    padding-left: 10px;
}

.input-buscar::placeholder{
    font-family: 'Roboto Slab', serif;
    color: #001242;
}

.input-buscar:focus{
    border-color: transparent;
    outline-color: #09d4ff;
    background-color: white;
}