@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');

body{
  font-family: 'Roboto Slab', serif;
  /* background-color: #0081A7; */
  background: linear-gradient(18deg, rgba(163,193,255,1) 3%, rgba(9,212,255,1) 53%, rgba(14,2,255,1) 100%);
  background-repeat: no-repeat;
  /* background: linear-gradient(90deg, rgba(255,207,129,1) 0%, rgba(252,176,69,1) 51%, rgba(253,138,29,1) 100%); */
    /* background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(214,53,250,1) 68%, rgba(133,133,172,1) 100%); */
}

.contenedor{
  display: flex;
  justify-content: space-around;
  padding: 28px 25px;
}

.insertar-tareas{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: inherit;
  width: 30%;
  background-color: #FDFCDC;
  border: 4px solid #000022;
  border-radius: 20px;
}

.lista-tareas{
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: fit-content;
  width: 60%;
  background-color: #001242;
  border: 4px solid #001242;
  border-radius: 20px;
}

.div-lista{
  display: flex;
  justify-content: center;
  overflow-y: scroll;
  height: 390px;
}

/* Poder darle estilos a la barra */
.div-lista::-webkit-scrollbar {
  -webkit-appearance: none;
}

/* Grosor de la barra */
.div-lista::-webkit-scrollbar:vertical {
  width: 15px;
}

/* .div-lista::-webkit-scrollbar-button:increment,.div-lista::-webkit-scrollbar-button {
  display: none;
}  */

/* .div-lista::-webkit-scrollbar:horizontal {
  height: 10px;
} */

.div-lista::-webkit-scrollbar-thumb {
  background-color: #09d4ff;
  border-radius: 20px;
  border: 2px solid #001242;
}

.pie-pagina{
  background-color: #001242;
  display: flex;
  justify-content: center;
  padding: 5px;
}

@media (max-width: 640px){
  .contenedor{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .insertar-tareas{
    width: 100%;
    margin-bottom: 20px;
  }
  
  .lista-tareas{
    width: 100%;
  }
  
  .modalBackground{
    width: 90%;
    height: 68%;
}

  
}