.item{
    display: flex;
    list-style: none;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
    box-sizing: border-box;
    background-color: #FDFCDC;
    margin: 10px 0;
    border: 3px solid #001242;
    border-radius: 20px;
    height: 50px;
    width: 100%;
}

.text-tarea{
    color: #040F16;
    font-weight: bold;
}

.item-completed{
    opacity: 0.5;
}


